.tab_4 {
  margin-left: 40px;
  list-style-type: circle;
}
.main {
  position: relative;
  background-color: #aeaeae;
  min-height: 50vh;
}
.common_style .mtitle {
  font-size: 25px;
  background: white;
  border-radius: 10px;
  box-sizing: border-box;
  display: block;
  text-align: center;
  margin: 0px 0px 10px 0px;
}
.common_style .stitle {
  color: black;
  font-size: 16px;
  font-weight: 800;
  border-radius: 2px;
  box-sizing: border-box;
  display: inline-block;
  border-radius: 10px;
  padding: 5px 0px 5px 5px;
  min-width: 90%;
  min-height: 16px;
}
.common_style .sstitle {
  font-size: 14px;
  font-weight: 800;
  box-sizing: border-box;
  display: inline-block;
  border-radius: 2px;
  padding: 5px;
  margin: 10px 0px;
}
.common_style .title4 {
  font-size: 20px;
  font-weight: 500;
  box-sizing: border-box;
  display: inline-block;
  border-radius: 5px;
  padding: 10px;
  margin: 15px 0px 10px 0px;
}
.common_style .lblock {
  border: black solid 3px;
  width: 100%;
  box-sizing: border-box;
  display: inline-block;
  border-radius: 15px;
  font-size: 1em;
}

.common_style .lblock > .mblock {
  margin: 8px 0px 8px 0px;
  padding: 5px 5px 5px 5px;
}

.common_style .mblock {
  width: 100%;
  box-sizing: border-box;
  display: block;
  margin: 2px 0px;
  border-radius: 10px;
  min-height: 20px;
  padding: 10px;
  border: black solid 1px;
}

.common_style .sblock {
  width: 100%;
  box-sizing: border-box;
  display: block;
  border-radius: 10px;
  padding: 10px;
  margin: 5px 0px 5px 0px;
  min-height: 20px;
}
.common_style img {
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  max-width: 100%;
}

.common_style i {
  color: #ff3d00;
  font-weight: 800;
  font: caption;
  font-size: 15px;
  word-break: break-all;
}

@media screen and (min-width: 1024px) {
  .common_style {
    display: inline-block;
    width: 100%;
  }
}

@media screen and (max-width: 1024px) {
  .common_style {
    display: inline-block;
    width: 100%;
  }
}
/* ÏïÑÎûòÎ∂ÄÌÑ∞Í∞Ä ÏÇ¨Ïö©Ìï† css */

.common_style {
  width: 100%;
  display: inline-block;
}
.common_style img {
  text-align: center;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.common_style small {
  color: white;
  background-color: green;
  padding: 2px;
  border-radius: 5px;
}
.common_style .h2 {
  margin: 20px 0px 10px 10px;
  padding: 5px 5px;
  display: inline-block;
  font-size: 18px;
  font-weight: 800;
  background-color: orange;
}
.common_style .h3 {
  color: #3366ff;
  margin-left: 15px;
}
.common_style .h4 {
  color: #ff33cc;
}
.common_style .h5 {
}
.common_style details::before {
  content: "üåù";
}
.common_style details[open]::before {
  content: "üåö";
  display: inline-block;
}
.common_style summary {
  display: inline-block;
  width: 90%;
  padding-left: 10px;
}
.common_style summary:hover {
  color: red;
}

